.user--tile {
    max-width: 210px;
    width: 100%;
    background-color: #fff;
    border-radius: .5rem;
}

.user--img {
    height: 210px;
}

.user--img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .5rem .5rem 0 0;
}

.user--tile__content {
    padding: .7rem;
}

.user--tile__content h3 {
    font-weight: 600;
    margin-bottom: .5rem;
}

.user--tile__content button {
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    font-weight: 600;
    padding: .5rem 1rem;
}

.add-friend--btn {
    background-color: #E7F3FF;
    color: var(--primary-color);
}

.add-friend--btn:hover {
    background-color: #d6e2ee;
}

.delete-tile--btn {
    color: #050505;
    background-color: #dddee3;
}

.delete-tile--btn:hover {
    background-color: #cccdd2;
}
















